
const Article = (props) => {

    const { urlToImage, url, title, description, source } = props.article;

    let image = '';
    let dftImg = 'https://static.coindesk.com/wp-content/uploads/2018/03/BTC-chart-1-860x430.jpg'

    if(urlToImage !== ''){
        image= <img src={urlToImage} alt={title} className="card-img-top"/>
    } else {
        image = <img src="public/defaultbitimg.jpg" alt={title} className="card-img-top"/>
    }

    return (
        <div className="col-md-6 col-12 mb-4">
            <div className="card">
                <div className="card-image">
                    {image}
                </div>
            </div>
        </div>
    )
}

export default Article;